
var cfg_edit = {};

const cfg_edit_col_th = [
    {
        type: 'index',
        title: '#',
        key: 'id',
        width: '200px',
        align: 'center'
    },
    {
        title: 'Model Name',
        key: 'model_name',
        //editable: true,
        align: 'center'
    },
    {
        title: 'Templet Name',
        key: 'template_name',
        //editable: true,
        align: 'center'
    },
    {
        title: '操作',
        key: 'handle',
        width: '200px',
        align: 'center',
        handle: ['editForm', 'delete']
    }
];

const cfg_vap_edit_col_th = [
    {
        type: 'index',
        title: '#',
        key: 'id',
        width: '200px',
        align: 'center'
    },
    {
        title: 'Profile Name',
        key: 'profiles_name',
        //editable: true,
        align: 'center'
    },
    {
        title: 'SSID',
        key: 'ssid',
        //editable: true,
        align: 'center'
    },
    {
        title: 'Security',
        key: 'security',
        //editable: true,
        align: 'center'
    },
    {
        title: '操作',
        key: 'handle',
        width: '200px',
        align: 'center',
        handle: ['editForm']
    }
];


export default cfg_edit = {
    cfg_edit_col_th: cfg_edit_col_th,
    cfg_vap_edit_col_th: cfg_vap_edit_col_th
};


