<style>
    @import "./style/config.css";
</style>

<template>
    <div>
        <div v-if="selected_model === ''">
            <Row>
                <Col span="12" offset="6">
                    <select-form :tips="'请选择或输入机种名称'"
                                 :optionList="optionList"
                                 @select-changed="selectChanged"
                                 :selectOption="selected_model"
                    ></select-form>
                </Col>
            </Row>
        </div>
        <div v-else>
            <Row>
                <Col span="12" offset="6">
                    
                    <select-form :tips="'请选择或输入机种名称'"
                                 :optionList="optionList"
                                 @select-changed="selectChanged"
                                 :selectOption="selected_model"
                    ></select-form>

                </Col>
            </Row>
            <Tabs type="card" v-model="selectdevice" @on-click="tab_device_handle">
                <TabPane v-for="tab in tabs" :label="tab.label" :key="tab.key" :name="tab.key">
                    <model-table
                            v-model="tableData"
                            :columnsList="tableColumns"
                            :isLoading="tableLoading"
                            :size="size"
                            @on-change="handle_edit_wireless"
                    ></model-table>
                </TabPane>
            </Tabs>
            <Row>
                <page-table
                        :count="totalsize"
                        @on-change="changePage"
                        @on-page-size-change="changePageSize"
                ></page-table>
           </Row>
           <div v-if="edit_wireless === 'edit'">
                <Tabs type="card" v-model="selectcard" @on-click="tab_card_handle">
                    <TabPane v-for="tab in cardnum" :label="tab.label" :key="tab.key" :name="tab.key">
                        <Collapse accordion v-model="value3">
                            <Panel name="1">
                                Wireless Setting
                                <p slot="content">
                                    
                                    <model-form
                                            ref="formRef"
                                            v-model="modelFormData_wireless"
                                            :formItems="newModelForm_wireless">
                                    </model-form>

                                </p>
                            </Panel>
                            <Panel name="2">
                                VAP Profiles
                                <div slot="content">
                                    <model-table
                                            v-model="vaptableData"
                                            :columnsList="vaptableColumns"
                                            :isLoading="tableLoading"
                                            :size="size"
                                            @on-change="handle_edit_vap"
                                    ></model-table>
                                    <div v-if="edit_vap === 'edit'">
                                        <model-form
                                                ref="formRef"
                                                v-model="modelFormData_vap"
                                                :formItems="newModelForm_vap">
                                        </model-form>
                                    </div>
                                </div>
                            </Panel>
                        </Collapse>
                    </TabPane>
                </Tabs>
            </div>
        </div>
    </div>
</template>

<script>
    import selectForm from '../common/form/select-form.vue';
    import modelTable from '../common/table/table-list.vue';
    import pageTable from '../common/pagination/page-table.vue';
    import modelForm from '../common/form/best-form.vue';
    import util from '../../libs/util.js';
    import parser from '../../libs/parser.js';
    import templetData from './templetData.js';
    import cfg_edit_th from './cfg_edit_th.js';

    const optionList = [
        {
            label: 'ZN-7100-2DHO',
            value: 'ZN-7100-2DHO'
        },
        {
            label: 'AC-1027L',
            value: 'AC-1027L'
        },
        {
            label: 'XN-1033',
            value: 'XN-1033'
        }
    ];

    const tabs = [
        {
            label: '无线控制器',
            key: 'AC'
        },
        {
            label: '瘦AP',
            key: 'TAP'
        }
    ];

    const cardnum = [
        {
            label: 'Card 1',
            key: '0'
        },
        {
            label: 'Card 2',
            key: '1'
        }
    ];

    export default {
        components: {
            pageTable,
            selectForm,
            modelTable,
            modelForm
        },
        data () {
            return {
                value3: '1',
                size: 'small',
                totalsize: 0,
                page_size: 10,
                optionList:optionList,
                tabs:tabs,
                cardnum:cardnum,
                selectdevice: 'AC',
                selectcard: '0',
                selected_model: '',
                edit_wireless: '',
                edit_vap: '',
                tableData: [],
                vaptableData: [],
                tableLoading: false,
                tableColumns: [],
                vaptableColumns: [],
                vap_object0: {},
                vap_object1: {},
                vap_object2: {},
                vap_object3: {},
                vap_object4: {},
                vap_object5: {},
                vap_object6: {},
                vap_object7: {},
                vap_array: [],
                newModelForm_wireless: [],
                modelFormData_wireless: {},
                newModelForm_vap: [],
                modelFormData_vap: {},
                templetData: {},
                baseuri: '/templates/'
            };
        },
        methods: {
            //初始表单数据
            initFormData: function (event,cardnum) {
                //console.log('###########event#################',event,cardnum);
                this.newModelForm_wireless.forEach((item, index) => {
                    if (typeof(item.key) !== 'undefined'){
                        for(var key in event){
                            if(item.key === key){
                                var dataStrArr=event[key].split(",");//分割成字符串数组
                                var dataIntArr=[];//保存转换后的整型字符串
                                dataStrArr.forEach(function(data,index,arr){  
                                    dataIntArr.push(+data);  
                                });
                                delete this.modelFormData_wireless[item.key];
                                //console.log('###########dataIntArr[0]#################',item.key,dataIntArr[0]);
                                this.$set(this.modelFormData_wireless, item.key, dataIntArr[0]);
                            }
                        }
                    }
                });
                
                this.newModelForm_vap.forEach((item, index) => {
                    if (typeof(item.key) !== 'undefined')
                        this.$set(this.modelFormData_vap, item.key, 'lad');
                });
            },
            //切换设备类型标签
            tab_device_handle (event) {
                console.log('########tab device event############',event);
            },
            //切换card类型标签
            tab_card_handle (event) {
                //console.log('#########tab card event##############',event);
                this.show_wireless(event);
                this.show_vap(event);
            },
            //切换页面
            changePage (current) {
                let params = {
                    page: current,
                    limit: this.page_size
                };
                this.tableLoading = true;
                util.ajax.request('get', this.baseuri, params, {}, this.response_callback, this.exception_callback);
            },
            //改变pagesize
            changePageSize (page_size) {
                this.page_size = page_size;
                let params = {
                    limit: this.page_size
                };
                this.tableLoading = true;
                util.ajax.request('get', this.baseuri, params, {}, this.response_callback, this.exception_callback);
            },
            //筛选条件发生变化
            selectChanged: function (event) {
                //console.log('#######event#######',event);
                this.selected_model = event;
                let params = {
                    model_name: event,
                    limit: this.page_size
                };
                util.ajax.request('get', this.baseuri, params, {}, this.response_callback, this.exception_callback);
            },
            //编辑基本配置
            handle_edit_wireless (row_data, index) {
                //console.log('########row_data#########',row_data[index]);
                this.edit_wireless = 'edit';
                this.vap_profile_name = row_data[index].profiles_name.split(',');
                this.vap_ssid = row_data[index].ssid.split(',');
                this.vap_security = row_data[index].security.split(',');

                this.resolve_vap(this.vap_profile_name,this.vap_ssid,this.vap_security);

                this.show_wireless('0');//点击编辑时，默认先显示card1
                this.show_vap('0');//点击编辑时，默认先显示card1

                this.initFormData(row_data[index],0);
            },
            handle_edit_vap () {
                this.edit_vap = 'edit';
            },
            //解析vap
            resolve_vap(vap_profile_name,vap_ssid,vap_security) {
                this.vap_array.splice(0, this.vap_array.length);//每次重载之前清空数组

                this.$set(this.vap_object0, 'profiles_name', vap_profile_name[0]);
                this.$set(this.vap_object0, 'ssid', vap_ssid[0]);
                this.$set(this.vap_object0, 'security', vap_security[0]);
                this.vap_array.push(this.vap_object0);
                this.$set(this.vap_object1, 'profiles_name', vap_profile_name[1]);
                this.$set(this.vap_object1, 'ssid', vap_ssid[1]);
                this.$set(this.vap_object1, 'security', vap_security[1]);
                this.vap_array.push(this.vap_object1);
                this.$set(this.vap_object2, 'profiles_name', vap_profile_name[2]);
                this.$set(this.vap_object2, 'ssid', vap_ssid[2]);
                this.$set(this.vap_object2, 'security', vap_security[2]);
                this.vap_array.push(this.vap_object2);
                this.$set(this.vap_object3, 'profiles_name', vap_profile_name[3]);
                this.$set(this.vap_object3, 'ssid', vap_ssid[3]);
                this.$set(this.vap_object3, 'security', vap_security[3]);
                this.vap_array.push(this.vap_object3);
                this.$set(this.vap_object4, 'profiles_name', vap_profile_name[4]);
                this.$set(this.vap_object4, 'ssid', vap_ssid[4]);
                this.$set(this.vap_object4, 'security', vap_security[4]);
                this.vap_array.push(this.vap_object4);
                this.$set(this.vap_object5, 'profiles_name', vap_profile_name[5]);
                this.$set(this.vap_object5, 'ssid', vap_ssid[5]);
                this.$set(this.vap_object5, 'security', vap_security[5]);
                this.vap_array.push(this.vap_object5);
                this.$set(this.vap_object6, 'profiles_name', vap_profile_name[6]);
                this.$set(this.vap_object6, 'ssid', vap_ssid[6]);
                this.$set(this.vap_object6, 'security', vap_security[6]);
                this.vap_array.push(this.vap_object6);
                this.$set(this.vap_object7, 'profiles_name', vap_profile_name[7]);
                this.$set(this.vap_object7, 'ssid', vap_ssid[7]);
                this.$set(this.vap_object7, 'security', vap_security[7]);
                this.vap_array.push(this.vap_object7);
               
                //console.log('%%%%%%%%this.vap_array%%%%%%%%%%',this.vap_array);
                let params = {
                    limit: this.page_size
                };
                util.ajax.request('get', this.baseuri, params, {}, this.response_callback, this.exception_callback);
            },
            show_wireless (event) {
                //templetData.viewDesc.splice(1,1);
                console.log('############templetData############',templetData);
                if(event === '0'){
                    this.newModelForm_wireless = parser.Json(templetData).card0;
                }else if(event === '1'){
                    this.newModelForm_wireless = parser.Json(templetData).card1;
                }

            },
            show_vap (event){
                templetData.viewDesc.splice(0,1);
                if(event === '0'){
                    this.newModelForm_vap = parser.Json(templetData).card0;
                }else if(event === '1'){
                    this.newModelForm_vap = parser.Json(templetData).card1;
                }
            },
            //ajax请求返回值处理
            response_callback(res) {
                this.tableLoading = false;
                this.tableData = res.data.body;
                this.vaptableData = this.vap_array;
                
                this.totalsize = res.data.count;
            },
            //ajax请求错误异常处理
            exception_callback(err) {
                console.log(err);
            }
        },
        created: function (){
            this.tableColumns = cfg_edit_th.cfg_edit_col_th;
            this.vaptableColumns = cfg_edit_th.cfg_vap_edit_col_th;
            
            this.tableLoading = true;

            // let params = {
            //     limit: this.page_size
            // };
            // util.ajax.request('get', this.baseuri, params, {}, this.response_callback, this.exception_callback);
        }
    };
</script>


